@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");
* { margin: 0; padding: 0; font-size: 16px; box-sizing: border-box; }

img { vertical-align: top; }

body { font-family: "Nanum Gothic", "Noto Sans KR", sans-serif; font-size: 16px; color: #111; background: repeating-linear-gradient(90deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.05) 1px, 0, transparent 5%) #333; }

#header { display: flex; height: 50px; overflow: hidden; justify-content: center; align-items: center; background-color: #fff; }

#header h1 { font-size: 18px; text-transform: uppercase; font-weight: 700; text-shadow: 1px 1px 0px #ccc,2px 2px 0px #ccc,3px 3px 0px #ccc,4px 4px 0px #ccc,5px 5px 0px #ccc,6px 6px 0px #ccc,7px 7px 0px #ccc,8px 8px 0px #ccc,9px 9px 0px #ccc,10px 10px 0px #ccc,11px 11px 0px #ccc,12px 12px 0px #ccc,13px 13px 0px #ccc,14px 14px 0px #ccc,15px 15px 0px #ccc,16px 16px 0px #ccc,17px 17px 0px #ccc,18px 18px 0px #ccc,19px 19px 0px #ccc,20px 20px 0px #ccc,21px 21px 0px #ccc,22px 22px 0px #ccc,23px 23px 0px #ccc,24px 24px 0px #ccc,25px 25px 0px #ccc,26px 26px 0px #ccc,27px 27px 0px #ccc,28px 28px 0px #ccc,29px 29px 0px #ccc,30px 30px 0px #ccc;; }

#header h1 a { color: inherit; }

#search { display: flex; position: absolute; right: 20px; }

#search input { height: 36px; margin-right: 10px; padding: 10px; border: 1px solid #ccc; box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.15); min-width: 300px; border-radius: 5px; outline: none; }

#search a { overflow: hidden; padding: 0 20px; color: #fff; text-decoration: none; line-height: 2; background: none; border: none; background-color: #333; border-radius: 5px; font-weight: 700; text-shadow: 1px 1px 0px #1a1a1a,2px 2px 0px #1a1a1a,3px 3px 0px #1a1a1a,4px 4px 0px #1a1a1a,5px 5px 0px #1a1a1a,6px 6px 0px #1a1a1a,7px 7px 0px #1a1a1a,8px 8px 0px #1a1a1a,9px 9px 0px #1a1a1a,10px 10px 0px #1a1a1a,11px 11px 0px #1a1a1a,12px 12px 0px #1a1a1a,13px 13px 0px #1a1a1a,14px 14px 0px #1a1a1a,15px 15px 0px #1a1a1a,16px 16px 0px #1a1a1a,17px 17px 0px #1a1a1a,18px 18px 0px #1a1a1a,19px 19px 0px #1a1a1a,20px 20px 0px #1a1a1a;; }

#contents { padding: 70px 0 100px; }

#contents .container { width: 1440px; margin: auto; }

#contents .container .subTitle { margin-bottom: 150px; font-size: 32px; color: #fff; text-transform: uppercase; font-weight: 400; }

#contents .container .subTitle strong { font-size: 32px; font-weight: 900; }

#contents .container .movieList { display: flex; flex-wrap: wrap; }

#contents .container .movieList li { display: flex; width: calc(50% - 20px); margin: 10px 10px 100px 10px; padding: 30px; color: #fff; box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.15); background-color: rgba(100, 100, 100, 0.75); border-radius: 0 20px 0 20px; }

#contents .container .movieList li a { display: flex; color: inherit; text-decoration: none; }

#contents .container .movieList li a .imgBox { position: relative; margin-top: -100px; }

#contents .container .movieList li a .imgBox::before { content: ""; display: block; position: absolute; top: 0; right: 0; bottom: 0; left: 0; box-shadow: inset 0 0 0 0 white; transition: all 0.25s ease; }

#contents .container .movieList li a .imgBox:hover:before { box-shadow: inset 0 0 5px 5px white; }

#contents .container .movieList li a .imgBox img { width: 200px; height: 300px; box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2); object-fit: cover; }

#contents .container .movieList li a .imgBox .point { display: flex; width: 50px; height: 50px; overflow: hidden; position: absolute; bottom: 10px; left: 10px; color: #fff; background-color: #0555c6; border-radius: 100px; justify-content: center; align-items: center; font-weight: 700; text-shadow: 1px 1px 0px #044094,2px 2px 0px #044094,3px 3px 0px #044094,4px 4px 0px #044094,5px 5px 0px #044094,6px 6px 0px #044094,7px 7px 0px #044094,8px 8px 0px #044094,9px 9px 0px #044094,10px 10px 0px #044094,11px 11px 0px #044094,12px 12px 0px #044094,13px 13px 0px #044094,14px 14px 0px #044094,15px 15px 0px #044094,16px 16px 0px #044094,17px 17px 0px #044094,18px 18px 0px #044094,19px 19px 0px #044094,20px 20px 0px #044094,21px 21px 0px #044094,22px 22px 0px #044094,23px 23px 0px #044094,24px 24px 0px #044094,25px 25px 0px #044094,26px 26px 0px #044094,27px 27px 0px #044094,28px 28px 0px #044094,29px 29px 0px #044094,30px 30px 0px #044094;; }

#contents .container .movieList li a .info { margin-left: 30px; text-align: left; }

#contents .container .movieList li a .info .titleBox { margin-bottom: 20px; }

#contents .container .movieList li a .info .titleBox h3 { font-size: 24px; }

#contents .container .movieList li a .info .titleBox .date { margin-top: 10px; font-size: 14px; color: #999; }

#contents .container .movieList li a .info .overview { line-height: 1.25; height: 3.75em; overflow: hidden; display: -webkit-box; -webkit-line-clamp: 3; text-overflow: ellipsis; }

#contents .container .movieList li a .info .vote { margin-top: 20px; }

#contents .container .detailBox { display: flex; margin-top: -80px; }

#contents .container .detailBox .imgBox { box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2); }

#contents .container .detailBox .imgBox img { width: 400px; border: 10px solid #fff; box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2); border-radius: 0 50px 0 50px; }

#contents .container .detailBox .info { margin-left: 30px; padding: 30px 50px; text-align: left; box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2); flex-grow: 1; background-color: rgba(255, 255, 255, 0.9); border-radius: 50px 0 50px 0; }

#contents .container .detailBox .info .titleBox { position: relative; margin-bottom: 20px; padding-bottom: 20px; border-bottom: 1px solid #ccc; }

#contents .container .detailBox .info .titleBox h3 { font-size: 32px; }

#contents .container .detailBox .info .titleBox .btnList { position: absolute; top: 10px; right: 20px; padding: 10px 20px; color: #fff; background: #111; border: none; outline: none; border-radius: 5px; cursor: pointer; }

#contents .container .detailBox .info .summary dl { margin-bottom: 10px; padding-bottom: 10px; border-bottom: 1px dashed #ccc; }

#contents .container .detailBox .info .summary dl dt { margin-bottom: 5px; color: #666; }

#contents .container .detailBox .info .summary dl dd { font-size: 18px; font-weight: 700; }

#contents .container .detailBox .info .summary dl dd li { font-size: 18px; }

#contents .container .detailBox .info .summary .genreList { display: flex; }

#contents .container .detailBox .info .summary .genreList li { list-style: none; }

#contents .container .detailBox .info .summary .genreList li:first-child:before { display: none; }

#contents .container .detailBox .info .summary .genreList li:before { content: "/"; display: inline-block; padding: 0 10px; }

#contents .container .detailBox .info .overview dt { margin-bottom: 5px; color: #666; }

#contents .container .detailBox .info .overview dd { line-height: 1.25; height: 5em; overflow: hidden; display: -webkit-box; -webkit-line-clamp: 4; text-overflow: ellipsis; }

#contents .container .detailBox .bg { width: 100%; height: 100vh; position: fixed; top: 0; left: 0; z-index: -1; background-repeat: no-repeat; background-size: cover; }

#contents .container .detailBox .bg:before { content: ""; display: block; position: absolute; top: 0; right: 0; bottom: 0; left: 0; background-color: rgba(0, 0, 0, 0.5); }

#footer { display: flex; width: 100%; position: fixed; bottom: 50px; justify-content: center; }

#footer .copyright { padding: 10px 50px; font-size: 14px; text-transform: uppercase; background-color: #fff; border-radius: 100px; }

#footer .copyright strong { font-weight: 800; }
